import {addVisitRecord} from '../../api/api.js'
export default {
	data() {
		return {
			id: null,
			address:'',
			visitTime:'',//拜访日期
			customerName:'',
			matter:'',//拜访事项
			summary:'',//拜访摘要
			attachment:[
				{
					"file": "11",
					"original": "12",
					"size": "111",
					"title": "22",
					"type": "111",
					"url": "11111"
				}
			],
			expense:'',//费用报销
			name:'',
			position:'',
			intro:''
		};
	},
	onLoad(option) {
		this.id = option.id;
		this.name = decodeURIComponent(option.name);
		this.position = decodeURIComponent(option.position);
		this.intro = decodeURIComponent(option.intro);
	},
	computed: {
	        startDate() {
	            return this.getDate('start');
	        },
	        endDate() {
	            return this.getDate('end');
	        }
	    },
	methods:{
		/**
		 * 监听日期选择
		 */
		bindDateChange(e){
			const that= this;
			that.visitTime = e.target.value;
		},
		getDate(type) {
			const date = new Date();
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let day = date.getDate();

			if (type === 'start') {
				year = year - 60;
			} else if (type === 'end') {
				year = year + 2;
			}
			month = month > 9 ? month : '0' + month;;
			day = day > 9 ? day : '0' + day;
			return `${year}-${month}-${day}`;
		},
		upLoadFile(){
			const that= this;
			uni.chooseImage({
			    success: (chooseImageRes) => {
			        const tempFilePaths = chooseImageRes.tempFilePaths;
					console.log('tempFilePaths=',tempFilePaths)
					that.attachment[0] = tempFilePaths[0];
					
			    }
			});
		},
		toSaveInfo(){
			var params = {
				id:this.id,
				address:this.address,
				attachment:this.attachment,
				customerName:this.customerName,
				expense:this.expense,
				matter:this.matter,
				summary:this.summary,
				visitTime:this.visitTime,
			}
			addVisitRecord(params).then((res)=>{
				console.log('res=',res);
				uni.showToast({
					title:res.message
				})
				setTimeout(() => {
					uni.navigateBack()
				},1000)
			})
		}
	}
}
